import { Menu, Transition } from "@headlessui/react";
import { Fragment } from "react";
import { Link } from "react-router-dom";

const MenuElement = ({
  children,
  customClasses = "",
  menuBtnClasses = "",
  menuClasses = "",
  handleButton = () => {},
}) => {
  return (
    <div className={customClasses}>
      <Menu as="div" className={`relative ${menuClasses}`}>
        <div>
          <Menu.Button className={menuBtnClasses}>{children}</Menu.Button>
        </div>
        <Transition
          as={Fragment}
          enter="transition ease-out duration-100"
          enterFrom="transform opacity-0 scale-95"
          enterTo="transform opacity-100 scale-100"
          leave="transition ease-in duration-75"
          leaveFrom="transform opacity-100 scale-100"
          leaveTo="transform opacity-0 scale-95"
        >
          <Menu.Items className="absolute right-0 mt-2 w-full origin-top-right divide-y divide-gray-100 rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
            <div className="px-1 py-1 ">
              <Menu.Item>
                {({ active }) => (
                  <Link
                    to="/profile"
                    className={`${
                      active ? "bg-sky-600 text-white" : "text-gray-900"
                    } group flex w-full items-center rounded-md px-2 py-2 font-medium text-base capitalize`}
                  >
                    profile
                  </Link>
                )}
              </Menu.Item>
            </div>
            <div className="px-1 py-1 ">
              <Menu.Item>
                {({ active }) => (
                  <Link
                    to="/help"
                    className={`${
                      active ? "bg-sky-600 text-white" : "text-gray-900"
                    } group flex w-full items-center rounded-md px-2 py-2 font-medium text-base capitalize`}
                  >
                    help
                  </Link>
                )}
              </Menu.Item>
            </div>
            <div className="px-1 py-1 ">
              <Menu.Item>
                {({ active }) => (
                  <Link
                    to="/settings"
                    className={`${
                      active ? "bg-sky-600 text-white" : "text-gray-900"
                    } group flex w-full items-center rounded-md px-2 py-2 font-medium text-base capitalize`}
                  >
                    settings
                  </Link>
                )}
              </Menu.Item>
            </div>
            <div className="px-1 py-1 ">
              <Menu.Item>
                {({ active }) => (
                  <button
                    type="button"
                    className={`${
                      active ? "bg-sky-600 text-white" : "text-gray-900"
                    } group flex w-full items-center rounded-md px-2 py-2 font-medium text-base capitalize`}
                    onClick={handleButton}
                  >
                    logout
                  </button>
                )}
              </Menu.Item>
            </div>
          </Menu.Items>
        </Transition>
      </Menu>
    </div>
  );
};

export default MenuElement;
